









































import uniPopup from '../../../components/uni-popup.vue';
export default {
	data() {
		return {
			showPassword: true,
			showClearIcon: false,
			inputClearValue: "",
			loading:false,
			popType: 'middle',
			tiptitle: '提示',
			showPopupMiddle: false,
			tipcontent:'',
		}
	},
	onLoad() {

	},
	onPullDownRefresh() {
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onBackPress() {
	  uni.switchTab({
	  	url: '/pages/tabBar/user/user'
	  })
	},
	onNavigationBarButtonTap(e) {
	},
	methods: {
		goDetailPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
			this.showPopupTop = false;
			this.showPopupBottom = false;
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.popType = 'middle';
			this.showPopupMiddle = true;
		},
		changePassword: function () {
			this.showPassword = !this.showPassword;
		},
		bindClearInput: function (e) {
			this.inputClearValue = e.target.value;
			if (e.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function () {
			this.inputClearValue = "";
			this.showClearIcon = false;
		},
		formSubmit: function (e) {
			const account = e.detail.value.account;
			const password = e.detail.value.password;
			if(account ==='' ){
				uni.showModal({
					content:'请填写手机号',
					showCancel:false
				})
				return false;
			}
			
			if(password ==='' ){
				uni.showModal({
					content:'请填写密码',
					showCancel:false,
				})
				return false;
			}
			const that = this;
			try {
				this.$http.post(this.websiteUrl+'/api/api/doLogin',{account,password},(res) => {
					if(res.data.code==1){
						this.$store.dispatch('apiLogin',{openid:res.data.result.token,sessionKey:res.data.result.session_id});
						uni.switchTab({
							url: '/pages/tabBar/user/user'
						});
						//uni.navigateBack();
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:function() {
								that.loading = false;
							}
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false,
					success:function() {
						that.loading = false;
					}
				})
			}
			
			//console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		},
		getClause() {
			try {
				this.$http.post(this.websiteUrl+'/api/api/getClause',{},(res) => {
					if(res.data.code==1){
						this.tiptitle = '隐私条款';
						this.tipcontent = res.data.result.article.content.replace(/\\/g, "").replace(/<img/g, "<img style=\"display:none;\"");
						this.showMiddlePopup();
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		}
	},
	components: {
		uniPopup
	}
}
